import React, { useState } from 'react'
import Button from './Button'
import classes from './InputForm.module.css'
import ReactDOM from 'react-dom'

const Backdrop = (props) => {
    return <div className={classes.backdrop} onClick={props.cancel}></div>
}

const ModalOverlay = (props) => {

    return (
        <form className={classes.form} onSubmit={props.onSubmit}>
            <h2>{props.cardHeading}</h2>
            <div className={classes.titleInput}>
                <label htmlFor="title">title</label>
                <input type="text" value={props.enteredTitle} id="title" onChange={props.onTitleChange} />
            </div>
            <div className={classes.timeInput}>
                <div>
                    <label htmlFor="minutes">minutes</label>
                    <input className={classes.minutesInput} id="minutes" type="number" value={props.enteredMinutes} min="0" max="00:59" onChange={props.onMinutesChange} />
                </div>
                <div>
                    <label htmlFor="seconds">seconds</label>
                    <input className={classes.secondsInput} type="number" value={props.enteredSeconds} id="" onChange={props.onSecondsChange} />
                </div>
            </div>
            <div className={classes.buttonGroup}>
                <Button type="submit">{props.leftButtonLabel}</Button>
                <Button onClick={props.cancel} >cancel</Button>
            </div>
        </form>
    )
};


const InputForm = (props) => {
    const [enteredTitle, setEnteredTitle] = useState('')
    const [enteredMinutes, setEnteredMinutes] = useState('0')
    const [enteredSeconds, setEnteredSeconds] = useState('0')

    const formSubmitHandler = (event) => {
        event.preventDefault();
        const enteredFormData = {
            title: enteredTitle,
            time: parseInt(enteredMinutes * 60) + parseInt(enteredSeconds)
        };
        props.onSubmit(enteredFormData); // send the form data to interested component

        // clear the form data
        setEnteredTitle('');
        setEnteredMinutes('0');
        setEnteredSeconds('0')
    }

    const titleChangeHandler = (event) => {
        setEnteredTitle(event.target.value);
    }

    const minutesChangeHandler = (event) => {
        setEnteredMinutes(event.target.value);
    }

    const secondsChangeHandler = (event) => {
        setEnteredSeconds(event.target.value);
    }


    return (
        <>
            {ReactDOM.createPortal(<Backdrop cancel={props.dismiss} />, document.getElementById('backdrop-root'))}
            {ReactDOM.createPortal(<ModalOverlay
                cardHeading={props.cardHeading}
                onSubmit={formSubmitHandler}
                enteredTitle={enteredTitle}
                onTitleChange={titleChangeHandler}
                onMinutesChange={minutesChangeHandler}
                onSecondsChange={secondsChangeHandler}
                cancel={props.dismiss}
                leftButtonLabel={props.leftButtonLabel}
            />, document.getElementById('modal-root'))}
        </>
    )

}

export default InputForm
