import React from 'react'
import './Timer.css'

function Timer(props) {
    const time = props.time;
    const minutes = ("0" + Math.floor(time / 60)).slice(-2);
    const seconds = ("0" + time % 60).slice(-2);

    return (
        <div className="timer">
            <span>
                <h2>{minutes}:{seconds}</h2>
            </span>
        </div>
    )
}

export default Timer
