import React, { useState } from 'react'
import Card from './Card'
import Timer from './Timer'
import Button from './UI/Button'
import './cardGroup.css'
import deleteIcon from './UI/icons/delete.png'
import editIcon from './UI/icons/edit.png'
import InputForm from './UI/InputForm'




function GardGroup(props) {
    const [editing, setEditing] = useState(false)
    const [editedCard, setEditedCard] = useState(null)

    const timers = props.timers

    const editCardHandler = (event) => {
        setEditing(true)
        setEditedCard(event.target.id)
    }

    const dismissEditModalHandler = () => {
        setEditing(false)
        setEditedCard(null)
    }

    const submitHandler = (formData) => {
        props.updateCard(editedCard, formData.title, formData.time)
        setEditing(false)
        setEditedCard(null)
    }

    // Return a list of timer cards
    const timersList = timers.map((timer) => {
        return (
            <div key={timer.id} className={`card ${props.current === timer.id && 'current'}`}>
                <Card>
                    <img className="deleteButton" onClick={props.delete} id={timer.id} src={deleteIcon} alt="delete" />
                    <img className="deleteButton" onClick={editCardHandler} id={timer.id} src={editIcon} alt="delete" />
                    <h1 id={timer.id}>{timer.title}</h1>
                    <Timer time={timer.time} />
                </Card>
            </div>
        )
    })

    return (
        <div className="card-group">
            {timersList}
            {props.timers.length > 0 && <Button onClick={props.start}>{props.timerRunning ? 'stop' : 'start'}</Button>}
            <Button onClick={props.addTimer} >Add new</Button>
            {editing && <InputForm // 
                cardHeading="Edit Timer"
                dismiss={dismissEditModalHandler}
                onSubmit={submitHandler}
                leftButtonLabel='update'
            />}
        </div>
    )
}

export default GardGroup
