import React, { useState, useEffect } from 'react'
import { useSound } from 'use-sound'
import bell from './sounds/bell.wav'



import './App.css';
import CardGroup from './components/CardGroup'
import InputForm from './components/UI/InputForm'



// const timerCards = []

function App() {
  const [timerStarted, setTimerStarted] = useState(false)
  const [allTimers, setAllTimers] = useState([])
  const [currentTimer, setCurrentTimer] = useState(0)
  const [currentTimerId, setCurrentTimerId] = useState('')
  const [addingNewTimer, setAddingNewTimer] = useState(false);
  const [playBell] = useSound(bell)



  useEffect(() => setTimerStarted(false), [])

  useEffect(() => {
    if (allTimers.length > 0) {
      const current = allTimers[currentTimer].id
      setCurrentTimerId(current)
      console.log(currentTimerId);
    }
  }, [allTimers, currentTimer, currentTimerId])

  useEffect(() => {
    // Stop the countdown when final timer reaches zero
    if (currentTimer === (allTimers.length - 1) && allTimers[currentTimer].time === 0) {
      setTimerStarted((prevState) => prevState = false);
      setCurrentTimer(0);
      playBell()
    }
  }, [setTimerStarted, allTimers, currentTimer, playBell])



  useEffect(() => {
    let interval = null;
    const allTimersCopy = [...allTimers]

    if (timerStarted) {
      const remaining = allTimersCopy[currentTimer].time
      if (remaining <= 0 && currentTimer < allTimers.length - 1) {
        setCurrentTimer((prevState) => prevState + 1);
        playBell()
      }
      interval = setInterval(() => {
        allTimersCopy[currentTimer].time = allTimersCopy[currentTimer].time - 1
        setAllTimers(allTimersCopy)
      }, 1000)
    } else {
      clearInterval(interval)
    }
    return () => clearInterval(interval) // something about memory leaks
  }, [timerStarted, allTimers, currentTimer, playBell])


  function handleStart() {
    setTimerStarted(!timerStarted);
  }

  const formSubmitHandler = (formData) => {
    let id = Math.floor(Math.random() * 10000000); // add an id to the newly created timer
    formData = { ...formData, id }
    setAllTimers([...allTimers, formData])
    setAddingNewTimer(false);
  }

  const handleDelete = (event) => {
    console.log(event.target)
    const newArr = [...allTimers].filter((timer) => timer.id !== parseInt(event.target.id))
    setAllTimers(newArr);
  }

  const showNewTimerModal = () => {
    setAddingNewTimer(true);
  }

  const dismissNewTimerModal = () => {
    setAddingNewTimer(false)
  }

  const updateTimersList = (id, title, time) => {
    let newArr = [...allTimers]
    const currentlyEditingIndex = newArr.findIndex((timer) => timer.id === parseInt(id));
    newArr[currentlyEditingIndex].title = title;
    newArr[currentlyEditingIndex].time = time;
    setAllTimers(newArr)
    console.log('updated', id)
  }

  return (
    <div className="App">
      <CardGroup
        timers={allTimers}
        start={handleStart}
        delete={handleDelete}
        current={currentTimerId}
        addTimer={showNewTimerModal}
        timerRunning={timerStarted}
        updateCard={updateTimersList}
      />

      {addingNewTimer && // show new timer form if addingNewTimer is set to true
        <InputForm
          cardHeading="Create New Timer"
          onSubmit={formSubmitHandler}
          dismiss={dismissNewTimerModal}
          leftButtonLabel='submit'
        />}
      <footer>
        <span> Miguel Corkhill &copy;2021 |
          Icons made by <a href="https://www.freepik.com" title="Freepik">Freepik</a> from <a href="https://www.flaticon.com/" title="Flaticon">www.flaticon.com</a>
        </span>
      </footer>
    </div>
  );
}

export default App;
